<template>
  <div>
<div id="camera" style="width: 100%; height: calc(100vh - 70px)">
      <video id="videoTest" :playsInline="true" :controls="true" style="width: 100%; height: 100%; object-fit: fill" />
    </div>
    <canvas style="display: none" id="canvas" width="200" height="200"></canvas>
    <div class="footer2">
      <van-button class="submitButton" @click="camera=false" icon="cross"></van-button>
      <van-button class="submitButton" @click="uploadImg" icon="photograph"></van-button>
    </div>
    <div class="cover">
      <div>请将身份证或社保卡置于取景框内</div>
      <div id="mb" class="mb"></div>
    </div>
  </div>
</template>

<script>
// import func from 'vue-editor-bridge';
export default {
  data() {
    return {
      video: "",
      aCanvas: "",
      ctx: "",
      src: "",
      viWidth: "",
      viHeight: "",
    };
  },
  mounted() {
    //获取video，之后作为stream数据的载体
    this.video = document.getElementById("videoTest");
    this.aCanvas = document.getElementById("canvas");
    this.ctx = this.aCanvas.getContext("2d");   

    navigator.mediaDevices.getUserMedia({
        video: {
          facingMode: "environment", //在app上使用后置  后置:’environment‘,前置：’user‘    
        width: { min: 1024, ideal: 1280, max: 1920 },
        height: { min: 776, ideal: 720, max: 1080 }
        },
        audio: false, //关闭录音
      })
      .then((stream) => {
        this.video.srcObject = stream;
        this.mediaStream = stream;
        this.video.play();       
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    async uploadImg() {
      this.ctx.drawImage(this.video, 0, 0, 200, 200);
      this.src = this.aCanvas.toDataURL("image/*");      
      if (this.src) {
        this.$router.push({
          path: "/img",
          query: {
            src: this.src,
          },
        });
      } else {
        this.$toast({ message: "拍照失败" });
      }
    },
  },
};
</script>

<style scoped>
.cover{
 position: absolute;
 height: 40%;
  bottom: 0;
  top: 25%;
  right: 0;
  left:0;
  bottom: 0;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  transform: rotate(90deg);
}
.mb {
 
  width: 95%;
  height: 250px;
  background: linear-gradient(to left, white, white) left top no-repeat,
    linear-gradient(to bottom, white, white) left top no-repeat,
    linear-gradient(to left, white, white) right top no-repeat,
    linear-gradient(to bottom, white, white) right top no-repeat,
    linear-gradient(to left, white, white) left bottom no-repeat,
    linear-gradient(to bottom, white, white) left bottom no-repeat,
    linear-gradient(to left, white, white) right bottom no-repeat,
    linear-gradient(to left, white, white) right bottom no-repeat;
  background-size: 2px 10px, 10px 2px, 2px 10px, 10px 2px;
}
/* .mb{
    position: absolute;
    top: 0;right: 0;left: 0;  bottom: 0;  
    background: rgba(224, 224, 224,0.8);
    border: 0 solid #000;
    opacity: 0.75;    
    filter: alpha(opacity=75);
        z-index: 9;
    box-shadow: 0 0 0 100px #000;
    overflow: hidden;
    width: 167px;
    height: 39px;
    border-width: 200px 25px 100px 40px ;
    display: block;   
}
.mb::before {
        content: '';
        width: 100%;
        height:100%;
        z-index: 100;
        border-radius: 5%;
        border: 5px solid rgba(20,146,253);
        position: absolute;
        left: -5px; top: -5px;
        box-shadow: inset 0 0 5px 2px rgba(0,0,0,.75);
    } */
.submitButton {
  width: 50px;
  height: 50px;
  background: #fff;
  box-shadow: 0 0 5px #fff;
  border-radius: 50px;
  position: fixed;
  bottom: 10px;
  left: 50%;
  margin-left: -25px;
}
.footer {
  width: 100%;
  padding: 10px;
  height: 50px;
  background: white;
  display: flex;
  justify-content: space-around;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}
</style>


